import './Header.css'
import React from 'react';
import { Link } from 'react-router-dom'
import logo from '../../media/logo.png';

const Header = () => {
    return(
        <header style={{position: 'fixed', width: '100%', zIndex: '100'}} className="ui stackable menu header-component">
                <Link to="/" className="header" id="logo">
                    <img className="page-images" src={logo} alt="gsr-logo" />
                    <div className="content header-title-container">
                        <div className="header-header">GUSTAVO RIOS</div>
                        <div clasName="header-subheader">{"{fullStack webDeveloper}"}</div>
                    </div>
                </Link>
                <div className="right menu">
                    <Link to="/" className="item item-header">Portfolio</Link>
                    <Link to="/about" className="item item-header">About me</Link>
                    <Link to="/resume" className="item item-header">Resume</Link>
                </div>
        </header>
    );
}

export default Header;